import {getCurrentTab} from "./tabs";

export async function startGoogleOAuth(clientId: string, scopes: Array<string>) {
  if (!chrome?.identity) {
    console.error("chrome.identity is not defined")
  }
  const redirectUri = encodeURIComponent('https://' + chrome.runtime.id + '.chromiumapp.org');
  const url = 'https://accounts.google.com/o/oauth2/v2/auth' +
    '?nonce=' + new Date().getMilliseconds() +
    '&client_id=' + clientId +
    '&prompt=consent' +
    '&response_type=code' +
    '&access_type=offline' +
    '&redirect_uri=' + redirectUri +
    '&scope=' + encodeURIComponent(scopes.join(' '));

  const currentTabId = await getCurrentTab()
  chrome.identity.launchWebAuthFlow(
    {
      'url': url,
      'interactive': true
    },
    function (redirectedTo?: string) {
      if(redirectedTo === undefined){
        throw new Error("after login redirect is undefined")
      }
      if (chrome.runtime.lastError) {
        // Example: Authorization page could not be loaded.
        console.log(chrome.runtime.lastError.message);
      } else {
        const resUrl = new URL(redirectedTo)
        const code = resUrl.searchParams.get("code");
        chrome.tabs.sendMessage(currentTabId, {
          type: "setGoogleOauthCode", code
        });
      }
    }
  );
}
