// @ts-ignore
import reloadOnUpdate from "virtual:reload-on-update-in-background-script";
import {Account, startGoogleOAuth} from "@front-lib";
import * as Sentry from "@sentry/browser";

reloadOnUpdate("pages/background");

chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
    if (message.type === 'inboxsdk__injectPageWorld' && sender.tab) {
        if (chrome.scripting) {
            chrome.scripting.executeScript({ target: { tabId: sender.tab.id }, world: 'MAIN', files: ['pageWorld.js'] });
            sendResponse(true);
            return true
        } else {
            throw new Error('chrome.scripting is undefined, this means that scriptinjection is not supported on your browser');
            return false
        } }
});

chrome.runtime.onInstalled.addListener(async function (object) {
    const accounts = await Account.getAccountsData()
    if (object.reason == "install" || (accounts.length == 0 && object.reason == "update")) {
        const url = "https://mail.google.com"
        chrome.tabs.create({url});
    }
});
const ONE_DAY_IN_MILLISECONDS = 24 * 60 * 60 * 1000; // One day in milliseconds

chrome.runtime.onUpdateAvailable.addListener(details => {
    if (details) {
        chrome.storage.local.get('lastUpdate', data => {
            const lastUpdate = data.lastUpdate;
            const currentDate = new Date().getTime();
            if (!lastUpdate || currentDate - lastUpdate >= ONE_DAY_IN_MILLISECONDS) {
                try {
                    chrome.runtime.reload();
                    chrome.storage.local.set({ lastUpdate: currentDate });
                } catch (e) {
                    console.error(e);
                    Sentry.captureException(e, { tags: { "amplitude": "chrome_extension_updated" } });
                }
            } else {
                console.log('Maximum one update per day reached');
            }
        });
    }
});

chrome.runtime.onMessage.addListener(async (message, sender, sendResponse) => {
  if (message.type === 'startIdentityOAuth') {
    startGoogleOAuth(
      "812360763396-2e6tbtj82r0t37kgnl7a2nvv2qnlvf57.apps.googleusercontent.com",
      ["https://www.googleapis.com/auth/userinfo.email","https://www.googleapis.com/auth/userinfo.profile"]
    )
  }
    if (message.type == "setAccount") {
        try {
            const account = message.options.message;
            if (account.token) {
                await Account.createAccountEntry(account.email.toLowerCase())
                for (const key in account) {
                    await Account.editAccountAttribute(account.email.toLowerCase(), key, account[key]);
                }
            }
        } catch (e) {
            console.log(e)
        }
    }
    if (message.type == "setUser") {

    }

    return true
});


chrome.storage.onChanged.addListener((changes, namespace) => {
    for (let [key, {oldValue, newValue}] of Object.entries(changes)) {
        console.log(
            `Account Storage key "${key}" in namespace "${namespace}" changed.`,
            `Account Old value was "${JSON.stringify(oldValue)}", new value is "${JSON.stringify(newValue)}".`
        );
    }
});
